import React, { Component } from "react";
import { Tab, Container, Nav, Button, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import ConnectSteps from "./ConnectSteps";
import PublishSteps from "./PublishSteps";
import { HUBSPOT_URL } from "../../../../../actions/api";
import { connect } from "react-redux";
import { getUserInfo_afterChanges, SkipOnBoarding } from "../../../../../actions/loginActions";
import { TOAST_WARN } from "../../../../../utils";
import SupportVideo from "../../CommonComponents/Support/SupportVideo";
import { TAGSHOP_USER_HOME_DATA } from "../../../../../actions/sceneActions";

class Steps extends Component {
    state = {showVideo:false, activeTab:'', accordionTab:'0'}

    componentDidMount(){
        const {stepData , TAGSHOP_USER_HOME_DATA} = this.props
        TAGSHOP_USER_HOME_DATA()
        if(stepData) this.setState({stepData:stepData},()=>this.setState({activeTab: this.stepsLeft('gallery') == 0 ? 'publish' : 'connect'}))
    }

    componentWillReceiveProps(nextProps){
        const {stepData} = nextProps
        if(this.props.stepData != stepData){
            this.setState({stepData:stepData},()=>this.setState({activeTab: this.stepsLeft('gallery') == 0 ? 'publish' : 'connect'}))
        }
    }

    stepsLeft = (section) => {
        const {stepData} = this.state
        switch (section) {
            case 'gallery':
                var step = 2
                if (stepData.FEED_CREATED) step -= 1
                if (stepData.PRODUCT_ADDED) step -= 1
                //if (stepData.APPS_MORE_USED) step -= 1
                return step;

            case 'website':
                var step = 3
                if (stepData.WEBSITE_VISITED) step -= 1
                if (stepData.FEED_CUSTOMIZED) step -= 1
                if (stepData.CUSTOMIZATION_SAVED) step -= 1
                return step;
            case 'email':
                var step = 3
                if (stepData.EMAIL_VISITED) step -= 1
                if (stepData.EMAIL_PUBLISHED) step -= 1
                if (stepData.EMAIL_PLATFORM_SELECTED) step -= 1
                return step;
            case 'shopon':
                var step = 3
                if (stepData.SHOPON_VISITED) step -= 1
                if (stepData.SHOPON_BIO_ADDED) step -= 1
                if (stepData.SHOPON_LINK_COPIED) step -= 1
                return step;
            default:
                break;
        }
    }

    markComplete = (section) => {
        switch (section) {
            case 'gallery':
                //this.updateExtraData({ FEED_CREATED: true, CHOOSE_NETWORK: true, PRODUCT_ADDED: true, APPS_MORE_USED: true }, 'gallery')
                this.skipSteps({stepstype:"gallery"})
                break;

            case 'website':
                //this.updateExtraData({ WEBSITE_VISITED: true, FEED_CUSTOMIZED: true, CUSTOMIZATION_SAVED: true })
                this.skipSteps({stepstype:"website"})
                
                break;
            case 'email':
                //this.updateExtraData({ EMAIL_VISITED: true, EMAIL_PUBLISHED: true, EMAIL_PLATFORM_SELECTED: true })
                this.skipSteps({stepstype:"emailcampaigns"})
                break;
            case 'shopon':
                //this.updateExtraData({ SHOPON_VISITED: true, SHOPON_BIO_ADDED: true, SHOPON_LINK_COPIED: true },'shopon')
                this.skipSteps({stepstype:"shoponbio"})
                break;
            default:
                break;
        }
    }

    skipSteps = (data) => {
        SkipOnBoarding(data).then(res=> this.props.updateUserInfo(() => { this.props.navigate('/home') }))
    }

    checkPublishStep = (section) => {
        const {stepData} = this.state
        if (section == "website") {
            if (!(stepData?.WEBSITE_VISITED && stepData?.FEED_CUSTOMIZED)) TOAST_WARN("To proceed, complete the second step");
        }
        if (section == 'email') {
            if (!(stepData?.EMAIL_VISITED && stepData?.EMAIL_PUBLISHED)) TOAST_WARN("To proceed, complete the second step");
        }
        if (section == 'shopon') {
            if (!(stepData?.SHOPON_VISITED && stepData?.SHOPON_BIO_ADDED)) TOAST_WARN("To proceed, complete the second step");
        }
    }

    updateExtraData = (obj, type) => {
                if(type){ 
                    if(type == 'gallery') this.setState({activeTab: 'publish'}) 
                    else this.props.navigate('/home') 
                }
    }

    onClose= event=> this.setState({showVideo: !this.state.showVideo})

    handleAccordion = (event) => this.setState({accordionTab:event}) 

    render(){
        const {showVideo, stepData, activeTab, accordionTab} = this.state
        return  <div className="d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 flex-position-center full-content">
            {stepData ? <Container className='onboard_step'>
                <div className="mb-4 text-center">
                    <h3>Gettings Started with first Shoppable Gallery</h3>
                    <p>Complete these simple steps to get  your account up and running.</p>
                </div>
                <Tab.Container defaultActiveKey={this.stepsLeft('gallery') != 0 ? 'connect' : 'publish'} activeKey={activeTab} onSelect={key=> this.setState({activeTab:key})}>
                    <Card className="border-0 overflow-hidden">
                        <Nav variant="tabs" className="mb-4 d-flex">
                            <Nav.Item className="w-50">
                                <Nav.Link className="f-center fw-bold border-0 border-end" style={{borderTopRightRadius:0}} eventKey="connect">
                                    <div className="f-center rounded-circle w-22px h-22px border me-2 fs-9">1</div>
                                    Connect
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="w-50">
                                <Nav.Link className="f-center fw-bold border-0" style={{borderTopLeftRadius:0}} eventKey="publish">
                                    <div className="f-center rounded-circle w-22px h-22px border me-2 fs-9">2</div>
                                    Publish
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                        <Card.Body>
                                <Tab.Content>
                                    <Tab.Pane eventKey="connect">
                                        <ConnectSteps stepsLeft={this.stepsLeft} markComplete={this.markComplete} stepData={stepData}/>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="publish">
                                        <PublishSteps stepsLeft={this.stepsLeft} markComplete={this.markComplete} stepData={stepData} checkPublishStep={this.checkPublishStep} handleAccordion={this.handleAccordion} accordionTab={accordionTab}/>
                                    </Tab.Pane>
                                </Tab.Content>
                        </Card.Body>
                        <Card.Footer className='border-0'>
                            <div className='f-center justify-content-between flex-column flex-lg-row'>
                                <p className="my-3 text-muted text-center text-lg-start">If you have any questions then you can schedule a demo with our experts or explore the support center to get the step by step guide for Tagshop</p>
                                <div className="flex-nowrap d-flex flex-shrink-0 ms-lg-3">
                                    <Button variant='primary' onClick={()=> this.setState({showVideo:true})}>
                                        Example Gallery
                                    </Button>
                                    <Link className='btn btn-outline-primary ms-3' to={HUBSPOT_URL} target='_blank' title='Demo'>
                                        Book A Demo
                                    </Link>
                                </div>
                            </div>
                        </Card.Footer>
                    </Card>
                </Tab.Container>
                <SupportVideo  show={showVideo} link={'https://player.vimeo.com/video/977782960'} onClose={this.onClose}/>
            </Container> : null}
        </div>
    }
    
}

const mapStateToProps = (state) => {
    return {
        stepData: state.dashboardData?.dashboarddata?.extra_data
    }
}

const mapDispatchToProps = dispatch => {
    return {
        updateUserInfo: (callBack) => dispatch(getUserInfo_afterChanges(callBack)),
        onSkipSteps : (data) => dispatch(SkipOnBoarding(data)),
        TAGSHOP_USER_HOME_DATA: () => dispatch(TAGSHOP_USER_HOME_DATA()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Steps)