import { Accordion, Button, ProgressBar } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { Component } from "react";

export default class PublishSteps extends Component {
    state={activeTab:'0'}

    componentWillReceiveProps(nextProps){
        if(JSON.stringify(this.props.stepData) != JSON.stringify(nextProps.stepData)){
            this.checkActiveTab(nextProps.stepData)
        }
    }

    componentWillMount(){
        this.checkActiveTab(this.props.stepData)
    }

    checkActiveTab = (stepData) => {
        let active = (!stepData.WEBSITE_VISITED || !stepData.FEED_CUSTOMIZED || !stepData.CUSTOMIZATION_SAVED) ? '0' : (!stepData.EMAIL_VISITED || !stepData.EMAIL_PUBLISHED || !stepData.EMAIL_PLATFORM_SELECTED) ? '1' :(!stepData.SHOPON_VISITED || !stepData.SHOPON_BIO_ADDED) ? '2' : '-1'
        if(active == '-1') this.props.markComplete('shopon')
        this.setState({activeTab: active})
    }
    

    render(){
    const { activeTab } = this.state
    const { stepsLeft, markComplete, stepData, checkPublishStep } = this.props
    return <Accordion activeKey={activeTab} onSelect={(event)=> this.setState({activeTab:event})}>
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="overflow-hidden">
                        <div className="d-flex justify-content-between w-100 flex-wrap">
                            <div className="d-flex align-items-center text-nowrap me-10">
                                <span className="text-primary">
                                    <FontAwesomeIcon type={'light'} icon={'browser'} iconClass={'me-2 display-5'}/>
                                </span>
                                Website
                            </div>
                            {stepsLeft('website') ? <p className="text-gray-700 mb-0 f-center text-nowrap"><span className="fw-bold me-1">{`${stepsLeft('website')} Step Left`}</span>.<span className="ms-1">About 4 min.</span></p> : null}
                        </div>
                        <ProgressBar className="position-absolute bottom-0 start-0 w-100 rounded-0 h-2px" variant="success" now={(3 - stepsLeft('website')) * 33.3} label={`${(3 - stepsLeft('website')) * 33.3}%`} visuallyHidden />
                    </Accordion.Header>
                    <Accordion.Body>
                            <div className="f-between mb-5 flex-wrap flex-sm-nowrap">
                                <p className="my-2 text-muted text-medium">Customize, preview and publish your shoppable gallery across your eCommerce store: Home page, product and category pages or review page.</p>
                                {stepsLeft('website') ? 
                                <Button variant="secondary" size="sm" className="text-nowrap" onClick={() => markComplete('website')}>
                                    <FontAwesomeIcon icon={'check-circle'} iconClass={'me-1'}/>
                                    Mark as Complete
                                </Button> : <></>}
                            </div>
                            <div className="d-flex flex-column">
                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.WEBSITE_VISITED ? 'border-primary' : ''}`} to={`${localStorage.getItem('website_id') ? `/website/shopify/${localStorage.getItem('website_id')}` : "/publish"}`} title='eCommerce'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.WEBSITE_VISITED ? 'border-success' : ''}`}>
                                            <span className={stepData.WEBSITE_VISITED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-image fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Choose the gallery to display on your eCommerce website</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>
                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.FEED_CUSTOMIZED ? 'border-primary' : ''}`} to={`${localStorage.getItem('website_id') ? `/website/shopify/${localStorage.getItem('website_id')}` : "/publish"}`} title='CTA'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.FEED_CUSTOMIZED ? 'border-success' : ''}`}>
                                            <span className={stepData.FEED_CUSTOMIZED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-browser fs-2"></i></span>
                                        </span>
                                        <p className="mb-0 fw-medium">Next, customize your gallery by applying themes, and CTA buttons.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>

                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.CUSTOMIZATION_SAVED ? 'border-primary' : ''}`} to={`${stepData.WEBSITE_VISITED && stepData.FEED_CUSTOMIZED ? `/website/shopify/${localStorage.getItem('website_id')}` : ''}`} onClick={() => { checkPublishStep('website') }} title='Generate code'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.CUSTOMIZATION_SAVED ? 'border-success' : ''}`}>
                                            <span className={stepData.CUSTOMIZATION_SAVED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-rocket-launch fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Save changes and click on ‘Generate code.’ Choose where you want to display the gallery.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>
                            </div>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header className="overflow-hidden">
                        <div className="d-flex justify-content-between w-100 flex-wrap">
                            <div className="d-flex align-items-center text-nowrap me-10">
                                <span className="text-primary">
                                    <FontAwesomeIcon type={'light'} icon={'bullhorn'} iconClass={'me-2 display-5'}/>
                                </span>
                                Email Campaigns
                            </div>
                            {stepsLeft('email') ? <p className="text-gray-700 mb-0 f-center text-nowrap"><span className="fw-bold me-1">{`${stepsLeft('email')} Step Left`}</span>.<span className="ms-1">About 4 min.</span></p> : null}
                        </div>
                        <ProgressBar className="position-absolute bottom-0 start-0 w-100 rounded-0 h-2px" variant="success" now={(3 - stepsLeft('email')) * 33.3} label={`${(3 - stepsLeft('email')) * 33.3}%`} visuallyHidden />
                    </Accordion.Header>
                    <Accordion.Body>
                            <div className="f-between mb-5 flex-wrap flex-sm-nowrap">
                                <p className="my-2 text-muted text-medium">From customization to publishing, host email campaigns in a few simple steps.</p>
                                {stepsLeft('email') ? 
                                <Button variant="secondary" size="sm" disabled={stepsLeft('website') ? true : false} className="text-nowrap" onClick={() => markComplete('email')}>
                                    <FontAwesomeIcon icon={'circle-check'} iconClass={'me-1'}/>
                                    Mark as Complete
                                </Button> : <></>}
                            </div>
                            <div className="d-flex flex-column">
                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.EMAIL_VISITED ? 'border-primary' : ''}`} to={`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`} title='email Camp'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.EMAIL_VISITED ? 'border-success' : ''}`}>
                                            <span className={stepData.EMAIL_VISITED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-image fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Select the content gallery you want to showcase in your emails.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>

                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.EMAIL_PUBLISHED ? 'border-primary' : ''}`} to={`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`} title='email Camp'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.EMAIL_PUBLISHED ? 'border-success' : ''}`}>
                                            <span className={stepData.EMAIL_PUBLISHED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-rocket-launch fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Choose a layout for your emails campaign {'>'} select the content you want to display and "Publish."</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>

                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.EMAIL_PLATFORM_SELECTED ? 'border-primary' : ''}`} to={`${stepData.EMAIL_VISITED && stepData.EMAIL_PUBLISHED ? `/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`:''}`} onClick={() => checkPublishStep('email')} title='email Camp'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.EMAIL_PLATFORM_SELECTED ? 'border-success' : ''}`}>
                                            <span className={stepData.EMAIL_PLATFORM_SELECTED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-envelope fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Now, select your preferred email marketing platform and follow the steps provided.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>
                            </div>
                    </Accordion.Body>
                </Accordion.Item>

                <Accordion.Item eventKey="2">
                    <Accordion.Header className="overflow-hidden">
                        <div className="d-flex justify-content-between w-100 flex-wrap">
                            <div className="d-flex align-items-center text-nowrap me-10">
                                <span className="text-primary">
                                    <FontAwesomeIcon type={'light'} icon={'link'} iconClass={'me-2 display-5'}/>
                                </span>
                                Shopon.Bio
                            </div>
                            {stepsLeft('shopon') ? <p className="text-gray-700 mb-0 f-center text-nowrap"><span className="fw-bold me-1">{`${stepsLeft('shopon')} Step Left`}</span>.<span className="ms-1">About 4 min.</span></p> : null}
                        </div>
                        <ProgressBar className="position-absolute bottom-0 start-0 w-100 rounded-0 h-2px" variant="success" now={(3 - stepsLeft('shopon')) * 33.3} label={`${(3 - stepsLeft('shopon')) * 33.3}%`} visuallyHidden />
                    </Accordion.Header>
                    <Accordion.Body>
                            <div className="f-between mb-5 flex-wrap flex-sm-nowrap">
                                <p className="my-2 text-muted text-medium">Customize, preview and publish your shoppable gallery across your eCommerce store: Home page, product and category pages or review page.</p>
                                {stepsLeft('shopon') ? 
                                <Button variant="secondary" size="sm" className="text-nowrap" onClick={() => markComplete('shopon')} disabled={stepsLeft('website') || stepsLeft('email') ? true : false}>
                                    <FontAwesomeIcon icon={'circle-check'} iconClass={'me-1'}/>
                                    Mark as Complete
                                </Button> : <></>}
                            </div>
                            <div className="d-flex flex-column">
                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.SHOPON_VISITED ? 'border-primary' : ''}`}  to={`/shopon-bio/preview/${localStorage.getItem('shopOn_id') ? localStorage.getItem('shopOn_id') : ''}`} title='shopon'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.SHOPON_VISITED ? 'border-success' : ''}`}>
                                            <span className={stepData.SHOPON_VISITED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-image fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Select the content gallery you want to collect content from.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>

                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.SHOPON_BIO_ADDED ? 'border-primary' : ''}`} to={`/shopon-bio/preview/${localStorage.getItem('shopOn_id') ? localStorage.getItem('shopOn_id') : ''}`} title='shopon-bio'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.SHOPON_BIO_ADDED ? 'border-success' : ''}`}>
                                            <span className={stepData.SHOPON_BIO_ADDED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-memo-circle-info fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Name your shop, add a small business description, and choose a unique username for your Shopon Bio.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>

                                <Link className={`p-2 f-between border rounded-1 text-muted w-100 mb-3 ${stepData.SHOPON_BIO_ADDED && stepData.SHOPON_VISITED ? 'border-primary' : ''}`} to={`${stepData.SHOPON_BIO_ADDED && stepData.SHOPON_VISITED ? `/shopon-bio/preview/${localStorage.getItem('shopOn_id') ? localStorage.getItem('shopOn_id') : ''}` : ''}`} onClick={() => checkPublishStep('shopon')} title='shopon-bio'>
                                    <div className="d-flex align-items-center">
                                        <span className={`icon-primary h-32px w-32px rounded-1 border f-center me-2 ${stepData.SHOPON_BIO_ADDED && stepData.SHOPON_VISITED ? 'border-success' : ''}`}>
                                            <span className={stepData.SHOPON_BIO_ADDED && stepData.SHOPON_VISITED ? 'text-success' : 'text-muted'}><i className="fa-duotone fa-solid fa-copy fs-2"></i></span>
                                        </span>
                                        <p className="mb-0  fw-medium">Copy the link, and voila! You can now add this shoppable link to your Instagram or TikTok bio.</p>
                                    </div>
                                    <div className="d-none d-sm-flex ms-3">
                                        <FontAwesomeIcon icon={'arrow-right'} iconClass={'fs-6 text-muted'}/>
                                    </div>
                                </Link>
                            </div>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
}}